"use client";

import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "@/store/hooks";
import { fetchStoryById, clearCurrent } from "@/features/story/slice";

export default function StoryPage({ params }: { params: { id: string } }) {
  const dispatch = useAppDispatch();
  const { current, currentStatus } = useAppSelector((s) => s.stories);

  useEffect(() => {
    dispatch(fetchStoryById(params.id));
    return () => {
      dispatch(clearCurrent());
    };
  }, [params.id, dispatch]);

  if (currentStatus === "loading" || !current) {
    return <div className="h-40 rounded-md border animate-pulse bg-muted" />;
  }

  return (
    <article className="prose dark:prose-invert max-w-3xl">
      <h1>{current.title}</h1>
      <p className="text-sm text-muted-foreground">by {current.author}</p>
      <hr />
      <div>{current.body}</div>
    </article>
  );
}
